*,*::before,*::after{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
a{
  all: unset;
}

/* slider start */
.switch {
  position: fixed;
  display: inline-block;
  width: 40px;
  height: 24px;
  right: 10px;
  top:10px;
  z-index: 10;
}
/* .switch>div{
  min-width: 50px;
  min-height: 50px;
  z-index: 10;
background-color: red;
} */
.switch input { 
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  -webkit-transition: .4s;
  transition: .4s;
}

.slider:before {
  position: absolute;
  content: "";
  height: 75%;
  width: 42%;
  left: 5%;
  bottom: 12%;
  background-color: white;
  -webkit-transition: .4s;
  transition: .4s;
}

input:checked + .slider {
  background-color: #2196F3;
}

input:focus + .slider {
  box-shadow: 0 0 1px #2196F3;
}

input:checked + .slider:before {
  transform: translateX(100%);
}

/* Rounded sliders */
.slider.round {
  border-radius: 20px;
}

.slider.round:before {
  border-radius: 50%;
}
/* slider end */
html{
  overflow-x: hidden;
}
body{
  --text-color:#ffffff;
  --background-color:#000000;
  --hover-color:#ffff00;
  color:var(--text-color);
  background-color: var(--background-color);
  overflow: hidden;
  height: 100vh;
}

.header{
  position: relative;
  top: 0px;
  transform: translateY(50%);
  height: 10vh;
  display: flex;
  z-index: 2;
  
}

.logo{
  display: flex;
  justify-content: center;
  align-items: center;
  user-select: none;
  width: 40%;
  font-family: 'Brush Script MT', cursive;
  transform: translateX(-20%);
}
.firstletter{
  font-size: 5em;
  transform: translateY(-10%);
}
.remaining{
  font-size: 2em;

}
.logo:hover{
  color: var(--hover-color);
  cursor: pointer;
}
.sections{
  display: flex;
  width: 40%;
  justify-content: space-around;
  align-items: center;
}
.sections div{
  display: inline-block;
  position: relative;
  user-select: none;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  width: 5em;
  text-align: center;
  transition:  0.5s;
}
.sections div:after{
  content: "";
  position: absolute;
  width: 100%;
  height: 2px;
  left: 0px;
  bottom:-5px;
  transform-origin: center;
  transform: scaleX(0);
  background-color: var(--hover-color);
  transition: 0.2s;
}
.sections div:hover:after,.sections div:hover{
  cursor: pointer;
  color: var(--hover-color);
  transform: scaleX(1);
  letter-spacing: 0.2em;

}

.mobilemenu{
  position: absolute;
  
  display: none;
  flex-direction: column;
  justify-content: space-between;
  top: 0.8em;
  right: 1em;
  width: 30px;
  height: 20px;
  z-index: 2;
  background: none;
  border: none;
}
.mobilemenu .bar{
  background-color: var(--text-color);
  width: 100%;
  height:2px;
  border-radius: 10px;
  
}

.p5Canvas{
  position: relative;
  z-index: -10;
}
@media only screen and (max-width: 768px) {
  
  .mobilemenu{
      display: flex;
      z-index: 10;
    }
  .sections{
      position: relative;
      display: flex;
      flex-direction: column;
      justify-content: flex-start;
      row-gap: 2em;
      height: 110vh;
      transform: translate(100%,-50px);
      top:17px;
      padding-top: 120px;
      right: 0px;
      background-color: hsla(0,0%,53%,0.5);
      backdrop-filter: blur(1rem);
      transition: 0.5s transform;
    
    }
  .sections[active="true"]{
      transform: translate(0%,-50px);
      transition: 0.5s transform;
      z-index: 3;
    }
  .logo{
      width: 60vw;
      transform: translateX(30%);
  }
  
}