#contact{
    position: relative;
    top:15vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap:1em;
}
#contact h1{
    font-family: "Cookie";
    word-spacing: 0.3em;
    font-size: 3em;
}
.socialmedia{
    width: min(60vw,600px);
    height : 50vh;
}
.row{
    height: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    align-items: center;
    align-content: center;
    gap: 6em;
}
.row a i{
    text-align: center;
 font-size: 3em;
 color:var(--text-color);
  
}

@media only screen and (max-width: 768px) {
  .socialmedia{
    width:80vw;
    
  }
  .row{
    gap:4em;
  }
}